@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh; }

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #f2f5f6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility; }

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff; }

a {
  color: #000000;
  text-decoration: none; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

body {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh; }
  body .header,
  body .footer {
    flex-shrink: 0; }
  body main {
    flex-grow: 1; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

@media (max-width: 767px) {
  .scroll-lock-ios {
    position: fixed;
    overflow: hidden; } }

@media (max-width: 767px) {
  .scroll-lock {
    overflow: hidden; } }

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none; }
  .no-scrollbar::-webkit-scrollbar {
    display: none; }

.input--error {
  outline: 2px solid #ff0000; }
  .input--error:focus {
    outline: 2px solid #ff0000; }

@media (max-width: 767px) {
  .overlay::before {
    content: "";
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: rgba(3, 16, 27, 0.7);
    z-index: 4; } }

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 18.22917%; }
  @media (max-width: 1023px) {
    .container {
      padding: 0 6.51042%; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 7.8125%; } }

.header {
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  position: fixed;
  width: 100%;
  padding: 30px 0 35px; }
  @media (max-width: 1023px) {
    .header {
      padding: 20px 0 25px; } }
  @media (max-width: 767px) {
    .header {
      padding: 15px 10px 20px 0; } }

.header__main-nav {
  display: flex;
  justify-content: space-between;
  align-items: flex-start; }

.header__logo img {
  width: 215px;
  height: 55px; }
  @media (max-width: 767px) {
    .header__logo img {
      width: 35px;
      height: 35px; } }

.header__menu {
  margin: 0;
  padding: 0;
  list-style: none;
  flex: 0 0 43%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 5px; }
  @media (max-width: 1023px) {
    .header__menu {
      flex: 0 0 70%;
      justify-content: flex-end; } }
  @media (max-width: 767px) {
    .header__menu {
      margin-top: -5px;
      flex: 0 0 80%; } }

@media (max-width: 1023px) {
  .header__link:not(:first-of-type) {
    margin-left: 20px; } }

.header__link a {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  color: #f2f5f6;
  font: 400 1em/1em "PT Mono", "Arial", sans-serif; }

@media (max-width: 1023px) {
  .header__link span {
    display: none; } }

@media (hover: hover), screen and (min-width: 1024px) {
  .header__link:hover span,
  .header__link:focus span {
    color: #00B2FF; } }

.header__link:active svg {
  stroke: #f2f5f6; }

.header__open-popup {
  display: block;
  margin-top: 5px;
  padding: 12px 15px;
  color: #f2f5f6;
  text-transform: uppercase;
  border: 1px solid #f2f5f6;
  border-radius: 3px;
  font: 700 0.88em/1em "Roboto Condensed", "Arial", sans-serif;
  letter-spacing: 5%;
  transition: transform 0.3s ease 0s; }
  @media (max-width: 1023px) {
    .header__open-popup {
      display: none; } }
  @media (hover: hover), screen and (min-width: 1024px) {
    .header__open-popup:hover, .header__open-popup:focus {
      color: #041827;
      background-color: #f2f5f6; } }
  .header__open-popup:active {
    transform: scale(0.9); }

